export default {
	
	onShow() {
		var star;
		var _self = this;
		function loadExecution() {
			const value = uni.getStorageSync('token');
			const value1 = uni.getStorageSync('phone');
			if (value && value1) {
				_self.global_.phone = value1;
				_self.global_.token = value;
				var timer = 3;
				var flag;
				function daoji() {
					timer = timer - 1;
					if (timer == 0) {
						uni.switchTab({
							url: '/pages/index/index'
						});
						clearInterval(flag);
						clearInterval(star);
					}
				}
				flag = setInterval(daoji, 500);
			} else {
				var timer1 = 3;
				var flag1;
				function daoji1() {
					timer1 = timer1 - 1;
					if (timer1 == 0) {
						uni.navigateTo({
							url: '/pages/leader/guide'
						});
						clearInterval(flag1);
						clearInterval(star);
					}
				}
				flag1 = setInterval(daoji1, 500);

				clearInterval(star);
			}
		}
		star = setInterval(loadExecution, 1000);
		
	}
};
